:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.App{min-height:100vh;display:grid;grid-template-rows:1fr auto;background-color:#c5e4e7;place-items:center}.App__logo{margin-block:2em}@media screen and (min-width: 1100px){.App{grid-template-rows:1fr auto 1fr}.App__logo{align-self:center;margin:0}}

:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.Calculator{background-color:#fff;border-start-start-radius:1em;border-start-end-radius:1em;display:flex;flex-direction:column;padding:1em;grid-gap:1em;gap:1em;max-width:600px}@media screen and (min-width: 1100px){.Calculator{border-radius:1em;flex-direction:row;max-width:920px;padding:1.5em;grid-gap:2em;gap:2em}.Calculator>*{flex-basis:50%}}

:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.CalculatorForm{-webkit-padding-before:0.4em;padding-block-start:0.4em;padding-inline:0.4em;display:flex;flex-direction:column;grid-gap:1em;gap:1em;flex-grow:0}@media screen and (min-width: 1100px){.CalculatorForm{grid-gap:2em;gap:2em}}

:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.Input{display:flex;flex-direction:column;grid-gap:0.3em;gap:0.3em}.Input__label{font-size:0.65rem;font-weight:700;color:#5e7a7d}.Input__field-wrapper{position:relative}.Input__icon{max-height:0.8em;pointer-events:none;position:absolute;left:0.75em;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.Input__field{-webkit-appearance:textfield;appearance:textfield;border:none;text-align:right;font-family:inherit;font-size:1rem;font-weight:700;color:#00494d;width:100%;height:100%;background:#f4fafa;border-radius:0.2em;padding:0.2em 0.75em}.Input__field:focus-visible{outline:2px solid #26c0ab}.Input__field--error{outline:2px solid red !important}.Input__error-msg{font-size:0.65rem;font-weight:700;color:red}

:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.Selector{display:flex;flex-direction:column;grid-gap:0.3em;gap:0.3em}.Selector__label{font-size:0.65rem;font-weight:700;color:#5e7a7d}.Selector__grid{display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:0.7em;gap:0.7em}@media screen and (max-width: 315px){.Selector__grid{grid-template-columns:1fr}}.Selector__option-content{display:inline-block;width:100%;height:100%;color:#fff;background-color:#00494d;border-radius:0.2em;padding:0.2em;text-align:center;font-weight:700;transition-property:color background-color;transition-duration:200ms;transition-timing-function:ease;cursor:pointer}.Selector__option-content:hover{color:#00494d;background-color:#c5e4e7}.Selector__option-radio{-webkit-appearance:none;appearance:none;opacity:0}.Selector__option-radio:checked ~ .Selector__option-content{background-color:#26c0ab;color:#00494d}@media screen and (min-width: 1100px){.Selector__grid{grid-template-columns:repeat(3, 1fr)}}

:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.Option__content{display:inline-block;width:100%;height:100%;color:#fff;background-color:#00494d;border-radius:0.2em;padding:0.2em;text-align:center;font-weight:700;transition-property:color background-color;transition-duration:100ms;transition-timing-function:ease;cursor:pointer}.Option__content:hover{color:#00494d;background-color:#c5e4e7}.Option__radio{-webkit-appearance:none;appearance:none;opacity:0}.Option__radio:checked ~ .Option__content{background-color:#26c0ab;color:#00494d}

:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.CustomOption{-webkit-appearance:textfield;appearance:textfield;border:none;text-align:right;font-family:inherit;font-size:1rem;font-weight:700;color:#00494d;width:100%;height:100%;background:#f4fafa;border-radius:0.2em;padding:0.2em 0.75em 0.2em 0.2em}.CustomOption:focus-visible{outline:2px solid #26c0ab}

:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.CalculatorOutput{background-color:#00494d;border-radius:0.5em;padding:1em;display:flex;flex-direction:column;grid-gap:0.6em;gap:0.6em}.CalculatorOutput__reset{background-color:#26c0ab;font:inherit;font-weight:700;color:#00494d;border:none;border-radius:0.2em;padding:0.2em;cursor:pointer;-webkit-margin-before:auto;margin-block-start:auto;transition-property:background-color, box-shadow, -webkit-transform;transition-property:background-color, box-shadow, transform;transition-property:background-color, box-shadow, transform, -webkit-transform;transition-duration:100ms;transition-timing-function:ease}.CalculatorOutput__reset:hover:not(:disabled){background-color:#c5e4e7;box-shadow:0 5px 0 rgba(0,0,0,0.4);-webkit-transform:translateY(-2px);transform:translateY(-2px)}.CalculatorOutput__reset:active:not(:disabled){transition-duration:50ms;box-shadow:none;-webkit-transform:none;transform:none}.CalculatorOutput__reset:disabled{opacity:0.2;cursor:not-allowed}@media screen and (min-width: 1100px){.CalculatorOutput{padding:2em}}

:root{font-family:"Space Mono",monospace;font-size:24px;font-weight:400}.Output{display:flex;justify-content:space-between;align-items:center}.Output__label{font-size:0.65rem;font-weight:700;color:#fff}.Output__unit{font-size:0.55rem;font-weight:700;color:#7f9c9f}.Output__value{font-size:1.4rem;font-weight:700;color:#26c0ab}@media screen and (min-width: 1100px){.Output__value{font-size:2rem}}

